import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/navigation";
import { Navigation } from "swiper/modules";
import s from "./Slider.module.scss";
import Slide from "../Slide/Slide";

export default function Slider({ movies, isLoading }) {
  return (
    <Swiper
      breakpoints={{
        300: {
          slidesPerView: 2,
        },
        550: {
          slidesPerView: 3,
        },
        800: {
          slidesPerView: 4,
        },

        1150: {
          slidesPerView: 6,
        },
      }}
      modules={[Navigation]}
      spaceBetween={10}
      slidesPerView={6}
      navigation
      className={s.slider}>
      {movies.map((el) => (
        <SwiperSlide key={el.id}>
          <Slide el={el} isLoading={isLoading}></Slide>
        </SwiperSlide>
      ))}
    </Swiper>
  );
}
